import React, {useState} from 'react'
import LoadingSpinner from './LoadingSpinner'
import ItemInList from './ItemInList'

export default function ListView({xc, sort_fn}) {

    console.log('render ListView')

    const format = (task_map) => {
        // Make list of tasks from map of tasks
        let list = []
        task_map.forEach((value, key)=>{
            list.push(value)
        })

        // order list of tasks
        let sorted_list = sort_fn(list)

        // format into jsx
        let formatted_list = []
        sorted_list.forEach((task)=> {
            formatted_list.push(
                <ItemInList t={task} key={task.id}/>
            )
        })
        return (
            <article className='d-flex pt-0 px-3 flex-column'>
                {formatted_list}
            </article>
        )
    }

    const [content, setContent] = useState()
    if (!content) {
        xc.get_all_tasks()
        .then((results) => {
            setContent(format(results))
        })
    }
    
    
    return (
        <>
            {content? content : <LoadingSpinner/>}
        </>
    )
}
export const SORT_LIST = {
    by_DUE(list) {
        list.sort((a,b)=>a.dueDate - b.dueDate)
        return list
    }
}