import React, { useState, createElement } from 'react'
import { xcContext } from './xcContext'
import Modal, { ModalContext } from './Modal'
import TaskForm from './TaskForm'
import Icon from './Icon'
import NotificationMaker from './NotificationMaker'
import ListView, { SORT_LIST } from './ListView'

const Dashboard = ({xc, client}) => {
    console.log('render dash')
    const [modalContent, setModalContent] = useState(false)
    const modalContext = {content: modalContent, setContent: setModalContent}
    const show_Modal_with_TaskFormSimple = function() {
        const hide_Modal = () => {
            setModalContent(null)
        }
        const task_form_simple = <TaskForm handleCloseForm={hide_Modal}/>
        setModalContent(task_form_simple)
    }

    const [mainContent, setMainContent] = useState()
    const display_task_Map = async function(map) {
        const results = await map()
        console.log(results)
        let formatted = ''
        results.forEach((value)=>{
            formatted = formatted.concat(JSON.stringify(value), '\n')
        })
        setMainContent(formatted)
    }

    const setMainTo = (view_fn) => {
        setMainContent(view_fn())
    }

    const view_Urgent = () => {
        return createElement(
            ListView,
            {xc: xc, sort_fn: SORT_LIST.by_DUE}
        )
    }
    
    const [activeTab, setActiveTab] = useState('All')
    function NavLink({clickCallback=null, i, t, highlight_after_click=true, dropdown=false}) {
        //console.log('render nav item')
        const handleClick = () => {
            if (highlight_after_click) {setActiveTab(t)}
            if (clickCallback) {clickCallback()}
        }
        const activeClasses = (t===activeTab)? 'text-primary-emphasis' : 'link-secondary'
        return (
            <a onClick={handleClick} href='# '
            className={`nav-link ${activeClasses} text-center text-decoration-none`}
            data-bs-toggle={dropdown? 'dropdown' : ''}
            >
                <div className='fs-3'><Icon i={i}/></div>
                <div style={{fontSize: 'small'}}>{t}</div>
            </a>
        )
    }

    const dropup = (
        <div className='nav-item dropup'>
            <NavLink dropdown={true} highlight_after_click={false} i='bi-three-dots' t='Options'/>                
            <ul className="dropdown-menu">
                <li><NavLink clickCallback={xc.signOut} i='bi-box-arrow-left' t='Sign Out'/></li>
                <li><NotificationMaker port_to_sw={client.port_to_sw} /></li>
            </ul>
        </div>
    )

    return (
        <xcContext.Provider value={xc}>
        <ModalContext.Provider value={modalContext}>
            <Modal modalContent={modalContent}/>
            <main className="overflow-x-hidden overflow-y-scroll flex-grow-1">{mainContent}</main>
            <nav className='nav nav-underline nav-justified bg-body-tertiary'>
                {dropup}
                <NavLink clickCallback={()=>display_task_Map(xc.get_all_tasks)} i='bi-hourglass-bottom' t='All'/>
                <NavLink clickCallback={()=>setMainTo(view_Urgent)} i='bi-list-task' t='List'/>
                <NavLink clickCallback={show_Modal_with_TaskFormSimple} i='bi-file-earmark-plus' t='New Task' highlight_after_click={false}/>
            </nav>
        </ModalContext.Provider>
        </xcContext.Provider>
    )
}

export default Dashboard;